import React from "react";
import { useState, useEffect } from "react";
import { Close } from "@carbon/icons-react";
import { Layer, Button, ExpandableSearch, TextInputSkeleton } from "@carbon/react";
import { Add, TrashCan, SidePanelCloseFilled } from "@carbon/icons-react";
// @ts-ignore
import { ContainedList, ContainedListItem } from "@carbon/react";
import $ from "jquery";

import "./SideChatList.scss";

import type { Chat, ChatList, Message } from "../../requests/request";


export interface ChangeEvent {
  target: HTMLInputElement;
  type: "change";
}

interface SideChatListProps {
	chats: ChatList | null;
	onSelectChat: (chat: Chat) => void;
	onNewChat: (e: any) => void;
	removeChat: (e: any) => void;
}

const SideChatList: React.FC<SideChatListProps> = ({ chats, onSelectChat, onNewChat, removeChat }) => {
	const [searchTerm, setSearchTerm] = useState<string>("");
	const [searchResults, setSearchResults] = useState<ChatList>([]);
  const [confirmationText, setConfirmationText] = useState<string>("");
  const [chatToRemove, setChatToRemove] = useState<Chat|null>(null);

	const expandableSearch = <ExpandableSearch placeholder="Filter" labelText="" value={searchTerm} onChange={(e) => handleChange(e)} closeButtonLabelText="Clear search input" size="lg" />;
	let results = {} as ChatList;

  function filterChats(chats: ChatList, searchTerm: string): ChatList {
    let results = {} as ChatList;
    for (const [key, value] of Object.entries(chats)) {
      const id = key as unknown as number;
      const chat = value as unknown as Chat;
			if (chat.name.toLowerCase().includes(searchTerm.toLowerCase())) {
				results = { ...results, [id]: chat };
			}
		}
    return results;
  }
  
	useEffect(() => {
		if (chats == null) return;
		results = filterChats(chats, searchTerm)
		setSearchResults(results);
	}, [searchTerm, chats]);

	function handleChange(event: ChangeEvent) {
		setSearchTerm(event.target.value);
	}

	function handleSelectChat(e: React.MouseEvent<HTMLLIElement, MouseEvent>) {
		const id = e.currentTarget.children[0].children[0].id as unknown as number;
		onSelectChat(chats![id]);
	}

  function closePopupIfOutside(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    // @ts-ignore
    if (e.target!.className == "popup-background") {
      setConfirmationText("");
    }
  }

  function handleConfirmRemoveChat(e: React.MouseEvent<HTMLButtonElement, MouseEvent>|null) {
    if (e != null) {
			// @ts-ignore
			let idToRemove = e.target!.parentNode.parentNode.parentNode.parentNode.id;
			if (idToRemove == "")
				// @ts-ignore
				idToRemove = e.target!.parentNode.parentNode.parentNode.id;
			const chatToRemove = chats![idToRemove as unknown as number];
      setChatToRemove(chatToRemove);
			setConfirmationText("Are you sure you want to delete this chat ?");
		} else {
      removeChat(chatToRemove!);
    }
  }

	return (
		<Layer className="side-menu-container">
			{confirmationText.length > 0 ? (
				<div className="popup-background" onClick={closePopupIfOutside}>
					<div className="confirmation-popup">
						<h1 className="popup-title">{confirmationText}</h1>
						<div className="popup-button-container">
							<Button kind="tertiary" onClick={() => setConfirmationText("")}>
								Cancel
							</Button>
							<Button
								kind="danger--tertiary"
								renderIcon={TrashCan}
								onClick={(e) => {
									setConfirmationText("");
									handleConfirmRemoveChat(null);
								}}
							>
								Delete
							</Button>
						</div>
					</div>
				</div>
			) : (
				<></>
			)}
			<Layer className="side-menu">
				<Button kind="secondary" className="new-chat-button" onClick={onNewChat}>
					New Chat
				</Button>
				<ContainedList type="" label="Chat list" className="chat-list" action={expandableSearch}>
					{chats == null
						? [...Array(5)].map((e, i) => {
								return (
									<ContainedListItem type="" id={i} key={i}>
										<TextInputSkeleton hideLabel />
									</ContainedListItem>
								);
						  })
						: Object.entries(searchResults).map((listItem, i) => {
                const chat = listItem[1] as unknown as Chat;
                const key = listItem[0] as unknown as number;
								return <ContainedListItem key={key} onClick={(e: any) => handleSelectChat(e)}>
									<div className="chat-list-item" id={key.toString()}>
										<h4 className="chat-name">{chat.name}</h4>
										<Button kind="ghost" size="sm" hasIconOnly onClick={(e) => handleConfirmRemoveChat(e)} iconDescription="remove" renderIcon={Close} />
									</div>
								</ContainedListItem>
              }
						  )}
				</ContainedList>
			</Layer>
		</Layer>
	);
};

export default SideChatList;
