import React, { useEffect, useState, useRef } from "react";
import styled from "styled-components";
import { SidePanelCloseFilled, Download } from "@carbon/icons-react";
import { TextInputSkeleton, Button, Theme, Search } from "@carbon/react";
import { Layer } from "@carbon/react";
import Table from './Table'

import type { Judgment } from "../../requests/request";

import "./judgmentPanel.scss";

interface JudgementTableProps {
	judgmentData: Judgment;
	closePanel: () => void;
}

const JudgmentTable: React.FC<JudgementTableProps> = ({ judgmentData, closePanel }) => {

	if (judgmentData == undefined) {
		return <></>;
	}

	function handleClose() {
    closePanel()
  }

	return (
		<div className="judgment-panel-container">
      <div className="judgment-panel-scroll">
        <Button kind="tertiary" className="close-button" renderIcon={SidePanelCloseFilled} onClick={handleClose}>
          Close
        </Button>
        <div className="panel-header">
          <h1 className="judgment-reference">Reference : {judgmentData.reference}</h1>
          <Button kind="primary" size="lg" className="download-button" renderIcon={Download}>
            Download in PDF
          </Button>
        </div>
        <div className="judgment-content">
          <h4 className="judgment-title">{judgmentData.name}</h4>
          <h4>Jurisdiction : {judgmentData.jurisdiction}</h4>
          <h4>Judges : {judgmentData.judges}</h4>
          <h4>Law : {judgmentData.law}</h4>
          <h4>Judgments refs: {judgmentData.judgments_refered}</h4>
          <h4>Type : {judgmentData.type}</h4>
          <h4>Keywords : {judgmentData.keywords}</h4>
          <h4>Week : {judgmentData.week}</h4>
          <h4>Year : {judgmentData.year}</h4>
        </div>
        <div className="judgment-summary">
          <h4 className="summary-title">
            Summary <span>#generated</span>
          </h4>
          <hr />
          <p className="summary">{judgmentData.summary}</p>
        </div>
        <div className="pdf-viewer-container">
          <object data="http://localhost:8000/database/judgment_files/dummy.pdf" type="application/pdf" width="100%" height="100%">
            <p>
              Alternative text - include a link <a href="http://localhost:8000/database/judgment_files/dummy.pdf">to the PDF!</a>
            </p>
          </object>
        </div>
      </div>
		</div>
	);
};

export default JudgmentTable;