// ChatView.tsx
import React, { useState, useEffect } from "react";
// @ts-ignore
import { ContainedList } from "@carbon/react";
import { Layer, Button, TextInput, ExpandableSearch, SelectableTile } from "@carbon/react";
// @ts-ignore
import { InlineLoading } from "@carbon/react";
import { chatRequest } from '../../requests/request';

import "./ChatView.scss";

import type { Chat, ChatList, Message } from "../../requests/request";
import type { ChangeEvent } from './SideChatList'

interface ChatViewProps {
	currentChat: Chat;
	username: string;
	setChats: (chats: ChatList) => void;
	lawCollections: string[];
}

const ChatView: React.FC<ChatViewProps> = ({ currentChat, username, setChats, lawCollections }) => {
	const [userInput, setUserInput] = useState<string>(""); // Specifies that userInput is a string
	const [searchTerm, setSearchTerm] = useState<string>("");
	const [errorMessage, setErrorMessage] = useState<string>("");
	const [searchResults, setSearchResults] = useState<string[]>([]);
	const [loading, setLoading] = useState<boolean>(false);
  const [selectedLaws, setSelectedLaws] = useState<string[]>([]);
	const [displayedChat, setDisplayedChat] = useState<Chat>(currentChat);

	const expandableSearch = <ExpandableSearch placeholder="Filter" labelText="" value={searchTerm} onChange={(e) => handleChange(e)} closeButtonLabelText="Clear search input" size="lg" />;

	const onSubmit = async () => {
		setLoading(true);
		chatRequest(currentChat, userInput, selectedLaws).then((chats) => {
      setUserInput("");
			if (chats != null) setChats(chats);
      setErrorMessage('');
			setLoading(false);
		}).catch((error) => {
      setErrorMessage(error.message);
			setLoading(false);
    });
    currentChat.messages.push({ role: "user", content: userInput });
    setDisplayedChat(currentChat);
	};

	useEffect(() => {
		const results = lawCollections.filter((id) => id.toLowerCase().includes(searchTerm.toLowerCase()));
		setSearchResults(results);
	}, [searchTerm, lawCollections]);

	function handleChange(event: ChangeEvent) {
		setSearchTerm(event.target.value);
	}

	function handleCollectionSelect(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
		const clickedLaw = e.currentTarget.textContent;
    if (selectedLaws.includes(clickedLaw!)) {
      setSelectedLaws(selectedLaws.filter((law) => law != clickedLaw));
    } else {
      setSelectedLaws([...selectedLaws, clickedLaw!]);
    }
	}

	return (
		<div className="chat-container">
			<div className="">
				<h1 className={"chatbox-title " + (currentChat.messages.length == 0 ? "" : "hidden")}>Ask Demetrius</h1>
				<ContainedList className={"textlaw-list " + (currentChat.messages.length == 0 ? "" : "hidden")} size="lg" isInset={true} kind="on-page" role="group" label="Textlaws" action={expandableSearch}>
					{searchResults.map((listItem, key) => (
						<SelectableTile name="lawlist" key={key} id={listItem} value={listItem} onClick={(e) => handleCollectionSelect(e)}>
							{listItem}
						</SelectableTile>
					))}
				</ContainedList>
			</div>
			<div className={"chatbox " + (currentChat.messages.length == 0 ? "hidden" : "")}>
				{currentChat.messages.map((message, index) => {
					if (message.role != "system") {
						return (
							<div key={index} className={"message " + message.role + "-message"}>
								<div className="message-header">
									<Logo />
									<h4>{message.role === "assistant" ? "Demetrius" : username}</h4>
								</div>
								<p>{message.content}</p>
							</div>
						);
					}
				})}
				{loading ? (
					<div key={"loading"} className={"message " + "assistant-message"}>
						<div className="message-header">
							<Logo />
							<h4>{"Demetrius"}</h4>
						</div>
						<InlineLoading status="active" iconDescription="Loading" description="Waiting for Demetrius..." />
					</div>
				) : (
					<></>
				)}
			</div>
			<h4 className="error-message">{errorMessage}</h4>
			<div className="message-typing-box">
				<TextInput className="message-input" id="chat-input" labelText="Message" enableCounter={true} type="text" value={userInput} onChange={(e) => setUserInput(e.target.value)} maxCount={5000} placeholder="Type your message..." onSubmit={onSubmit} disabled={loading} />
				<Button size="md" className="send-button" onClick={onSubmit} disabled={loading}>
					{loading ? <InlineLoading status="active" iconDescription="Loading" description="Sending message..." /> : "Send"}
				</Button>
			</div>
		</div>
	);
};

const Logo: React.FC = () => {
  return (
		<svg id="" className="message-logo" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 710.71 652.65">
			<path d="M427.91,326.54a127.79,127.79,0,0,1-97.67,124.18,245.27,245.27,0,0,0,0-248.36A127.79,127.79,0,0,1,427.91,326.54Z" />
			<path d="M515,326.22c.21,90.08-57.92,171.52-143.28,200.33-44.69,15.08-89.53,10.08-89.53,10.08-32.65-3.82-55.37-14.86-60.44-17.4a173.85,173.85,0,0,1-55.76-44.29c28.62,9.84,77.2,31.68,107,33.87A184.54,184.54,0,0,0,295.5,510c73.77-.11,141.81-45.88,169.73-114.08,17.45-42.38,18.32-91.42,1.94-134.26C442.44,194.88,377.91,147.12,306.77,143A185.6,185.6,0,0,0,229,154.22l-63.68,23.62a184.06,184.06,0,0,1,103.91-60.18s5.8-1,12.4-1.74c16.34-1.8,72.89-1.93,120.06,22.72C471.14,174.92,515.14,247.9,515,326.22Z" />
		</svg>
	);
}

export default ChatView;
