export type RowKeys = string[];
export type RowValues = string[];

export interface Chat {
	id: string;
	name: string;
	messages: Message[];
}

export interface ChatList {
  [id: number]: Chat; 
}

export interface Message {
	role: string;
	content: string;
}

const csrfToken:string = getMeta("csrf-token");

export async function chatListRequest(): Promise<ChatList|null> {
  const body = {}

  return backend_request("/api/chat/list/", 'GET', body).then((res) => {
    const chats = res.chats as ChatList;
    return chats
  }).catch((err) => {
    console.log(err)
    return null
  })
}

export async function chatRequest(chat: Chat, userInput: string, selectedLaws: string[]): Promise<ChatList | null> {
	const body = {
		chat: chat,
		user_query: userInput,
		selected_laws: selectedLaws,
	};
	return backend_request("/api/chat/", "POST", body)
		.then((res) => {
			const chats = res.chats as ChatList;
			return chats;
		})
		.catch((err) => {
			throw err;
		});
}

export async function removeChatRequest(chat: Chat): Promise<ChatList> {
  const body = {
    chat: chat,
  }
  return backend_request("/api/chat/remove/", 'POST', body).then((res) => {
    const message = res.message as string;
    const chats = res.chats as ChatList;
    return chats;
  }).catch((err) => {
    throw err
  })
}

