export async function createJudgmentRequest(judgment: Judgment, fileBuffer: Promise<ArrayBuffer>): Promise<string> {
  const address = csrfToken === "" ? "http://localhost:8000" : window.location.origin;
	const body = {
		judgment: judgment,
	};
  const array = new Uint8Array(await fileBuffer);
	return backend_request("/api/jst/insert/", "POST", body).then((res) => {
    const xhr = new XMLHttpRequest();
    xhr.open("POST", address + "/api/jst/insert/file/" + judgment['reference'] + '/', false);
    xhr.onerror = function (e) {
      throw e
    }
    xhr.onloadend = function (e) {
      console.log(xhr.responseText)
    }
    xhr.send(array)
    const message = res.message as string;
    return message;
  })
  .catch((err) => {
    throw err;
  });
}

