import React, { useEffect, useState, useRef } from 'react';
import SideChatList from "./SideChatList";
import ChatView from "./ChatView";
import { removeChatRequest, type RowKeys, type RowValues } from "../../requests/request";
import $ from 'jquery'
import { Add, TrashCan, SidePanelCloseFilled } from '@carbon/icons-react';
import { Modal, Button } from "@carbon/react";
// @ts-ignore
import { Layer } from "@carbon/react";
import './LawGPTFrame.scss'
import type { Chat, ChatList, Message } from "../../requests/request";

interface LawGPTFrameProps {
	chats: ChatList | null;
	lawCollections: string[];
	username: string;
	setChats: (chats: ChatList) => void;
}

const LawGPTFrame: React.FC<LawGPTFrameProps> = ({ chats, lawCollections, username, setChats }) => {
	const [currentChat, setCurrentChat] = useState<Chat | null>(null);

	useEffect(() => {
		const emptyChat = { id: "", name: "", messages: [] };
		if (chats == null) {
			setChats([emptyChat]);
		} else if (Object.keys(chats).length == 0) {
			setCurrentChat(emptyChat);
		} else if (currentChat == null) {
			setCurrentChat(chats![0]);
		} else if (currentChat.id == "") {
			setCurrentChat(chats![Object.keys(chats)[Object.keys(chats).length - 1] as unknown as number]);
		} else {
			setCurrentChat(chats![currentChat.id as unknown as number]);
		}
	}, [chats]);

	function handleSelectChat(chat: Chat) {
		setCurrentChat(chat);
	}

	function handleNewChat() {
		const newChat = { id: "", name: "", messages: [] };
		setCurrentChat(newChat);
    const new_chats = { ...chats, [newChat.id]: newChat };
		setChats(new_chats);
	}

  function removeChat(chatToRemove: Chat) {
    removeChatRequest(chatToRemove).then((chats) => {
			setChats(chats!);
			setCurrentChat(chats![0]);
		});
  }

	return (
		<div className="app-container">
			<SideChatList chats={chats} onSelectChat={handleSelectChat} onNewChat={handleNewChat} removeChat={removeChat}/>
			{currentChat != null ? <ChatView lawCollections={lawCollections} setChats={setChats} currentChat={currentChat} username={username} /> : <></>}
		</div>
	);
};

export default LawGPTFrame;