// Login.tsx
import React, { useState, useEffect } from "react";
import AuthenticationService from "./Authentification";
import { setCookie } from '../../requests/request';

interface LoginProps {
  setLoggedin: (loggedin: boolean) => void;
}

const Login: React.FC<LoginProps> = ({ setLoggedin }) => {
	const [username, setUsername] = useState("");
	const [password, setPassword] = useState("");
	const [loading, setLoading] = useState(false);
	const [message, setMessage] = useState("");
  
	const onChangeUsername = (e: React.ChangeEvent<HTMLInputElement>) => {
    const username = e.target.value;
		setUsername(username);
	};

	const onChangePassword = (e: React.ChangeEvent<HTMLInputElement>) => {
		const password = e.target.value;
		setPassword(password);
	};
  
	const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    
		setMessage("");
		setLoading(true);
    
		AuthenticationService.login(username, password).then(
      response => {
        // navigate("/profile");
        if (response) setLoggedin(true);
			},
			(error) => {
				const resMessage = (error.response && error.response.data && error.response.data.message) || error.message || error.toString();

				setLoading(false);
				setMessage(resMessage);
			}
		);
	};

  useEffect(() => {
    AuthenticationService.tryAuth().then((response) => {
      if (response.status == 200) setLoggedin(true);
    })
  }, []);

	return (
		<div className="col-md-12">
			<div className="card card-container">
				{/* Add your form HTML here */}
				<form onSubmit={handleLogin}>
					{/* Add your form fields here */}
					<div>
						<label htmlFor="username">Username</label>
						<input type="text" id="username" required value={username} onChange={onChangeUsername} />
					</div>

					<div>
						<label htmlFor="password">Password</label>
						<input type="password" id="password" required value={password} onChange={onChangePassword} />
					</div>

					<div>
						<button disabled={loading}>
							{loading && <span>Loading...</span>}
							<span>Login</span>
						</button>
					</div>

					{message && (
						<div>
							<div>{message}</div>
						</div>
					)}
				</form>
			</div>
		</div>
	);
};

export default Login;
