import React, { useEffect, useState } from "react";
import LawGPTFrame from "./components/lawchat/LawGPTFrame";
import "./App.scss";
import './fonts.css'
import Login from "./components/loginpage/LoginSection";
import Register from "./components/loginpage/RegisterSection";
import JudgementFrame from "./components/judgementspage/JudgementsFrame";
import { Layer, TileGroup, ClickableTile, RadioTile } from "@carbon/react";
// @ts-ignore
import { TreeView, TreeNode } from "@carbon/react";
import { chatListRequest, fetchJudgmentsRequest, fetchLawCollectionsRequest } from "./requests/request";

import type { Chat, ChatList, Judgment } from './requests/request';

const App: React.FC = () => {
	const [isloggedin, setLoggedin] = useState<boolean>(false);
	const [isInChatMode, setIsInChatMode] = useState<boolean>(true);
	const [isInJudgementMode, setIsInJudgementMode] = useState<boolean>(false);
  const [username, setUsername] = useState<string>('');
  const [chats, setChats] = useState<ChatList|null>(null);
  const [judgments, setJudgments] = useState<Judgment[]|null>(null);
  const [lawCollections, setLawCollections] = useState<string[]>([]);

  function switchToChatMode() {
    setIsInChatMode(true);
    setIsInJudgementMode(false);
  }
  function switchTojudgementMode() {
    setIsInChatMode(false);
    setIsInJudgementMode(true);
  }
  function switchToLawsMode() {
    
  }
  function switchToAccountsMode() {

  }

  const logingin = () => {
    setLoggedin(true);
  }

  useEffect(() => {
    (async function fetchData1 () {
      setChats(await chatListRequest());
    })();
    (async function fetchData2 () {
      setJudgments(await fetchJudgmentsRequest());
    })();
    (async function fetchData3 () {
      setLawCollections(await fetchLawCollectionsRequest());
    })();
  }, [isloggedin]);
  
  if (isloggedin)
    return (
			<div className="App">
				<Layer level={1} className="app-side-container">
					<div className="app-tittle-container">
						<svg id="logo" className="logo" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 710.71 652.65">
							<path d="M427.91,326.54a127.79,127.79,0,0,1-97.67,124.18,245.27,245.27,0,0,0,0-248.36A127.79,127.79,0,0,1,427.91,326.54Z" />
							<path d="M515,326.22c.21,90.08-57.92,171.52-143.28,200.33-44.69,15.08-89.53,10.08-89.53,10.08-32.65-3.82-55.37-14.86-60.44-17.4a173.85,173.85,0,0,1-55.76-44.29c28.62,9.84,77.2,31.68,107,33.87A184.54,184.54,0,0,0,295.5,510c73.77-.11,141.81-45.88,169.73-114.08,17.45-42.38,18.32-91.42,1.94-134.26C442.44,194.88,377.91,147.12,306.77,143A185.6,185.6,0,0,0,229,154.22l-63.68,23.62a184.06,184.06,0,0,1,103.91-60.18s5.8-1,12.4-1.74c16.34-1.8,72.89-1.93,120.06,22.72C471.14,174.92,515.14,247.9,515,326.22Z" />
						</svg>
						<h1 className="app-title">Demetrius</h1>
					</div>
					<TreeView level={0} className="tile-group" active="chats">
						<TreeNode id="judgment" className="tree-node tile-judgment" onClick={switchTojudgementMode} label="Judgments" />
						<TreeNode id="chats" className="tree-node tile-chats" onClick={switchToChatMode} label="Chats" />
						<TreeNode id="chats" className="tree-node tile-chats" onClick={switchToAccountsMode} label="Accounts" />
						<TreeNode id="chats" className="tree-node tile-chats" onClick={switchToLawsMode} label="Laws" />
					</TreeView>
				</Layer>
				{isInChatMode ? <LawGPTFrame lawCollections={lawCollections} setChats={setChats} username={username} chats={chats} /> : isInJudgementMode ? <JudgementFrame setJudgments={setJudgments} judgments={judgments} /> : <></>}
			</div>
		);
  else
    return (
			<div className="App">
				<Login setLoggedin={logingin} />
				<Register setLoggedin={logingin} />
			</div>
		);
};

export default App;
