// Register.tsx
import React, { useState } from "react";
import AuthenticationService from "./Authentification";

interface RegisterProps {
  setLoggedin: (loggedin: boolean) => void;
}

const Register: React.FC<RegisterProps> = ({ setLoggedin }) => {
	const [username, setUsername] = useState("");
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [successful, setSuccessful] = useState(false);
	const [message, setMessage] = useState("");

	const onChangeUsername = (e: React.ChangeEvent<HTMLInputElement>) => {
		const username = e.target.value;
		setUsername(username);
	};

	const onChangeEmail = (e: React.ChangeEvent<HTMLInputElement>) => {
		const email = e.target.value;
		setEmail(email);
	};

	const onChangePassword = (e: React.ChangeEvent<HTMLInputElement>) => {
		const password = e.target.value;
		setPassword(password);
	};

	const handleRegister = (e: React.FormEvent) => {
		e.preventDefault();

		setMessage("");
		setSuccessful(false);

		AuthenticationService.register(username, email, password).then(
			(response) => {
				setSuccessful(true);
				// navigate("/login"); // redirecting the user to the login page
			},
			(error) => {
				const resMessage = (error.response && error.response.data && error.response.data.message) || error.message || error.toString();

				setMessage(resMessage);
				setSuccessful(false);
			}
		);
	};

	return (
		<div className="col-md-12">
			<div className="card card-container">
				{/* Add your form HTML here */}
				<form onSubmit={handleRegister}>
					{!successful && (
						<div>
							{/* Add your form fields here */}
							<div>
								<label htmlFor="username">Username</label>
								<input type="text" id="username" required value={username} onChange={onChangeUsername} />
							</div>

							<div>
								<label htmlFor="email">Email</label>
								<input type="email" id="email" required value={email} onChange={onChangeEmail} />
							</div>

							<div>
								<label htmlFor="password">Password</label>
								<input type="password" id="password" required value={password} onChange={onChangePassword} />
							</div>

							<div>
								<button>
									<span>Sign Up</span>
								</button>
							</div>
						</div>
					)}

					{message && (
						<div className="form-group">
							<div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
								{message}
							</div>
						</div>
					)}
				</form>
			</div>
		</div>
	);
};

export default Register;

