// AuthenticationService.ts
import axios from "axios";
import { getMeta } from "../../requests/request";
import { setCookie, getCookie } from "../../requests/request";

const API_URL = "http://localhost:8000/user/";

class AuthenticationService {
  tryAuth() {
    return fetch(API_URL + "auth/", {
			method: "GET",
			headers: {
				"Content-Type": "application/; charset=UTF-8",
				"X-CSRFToken": getMeta("csrf-token"),
				"X-Requested-With": getCookie("access_token") as string,
			},
		});
  }
  
	login(username: string, password: string) {
		return axios
			.post(API_URL + "login/", {
				username,
				password,
			})
			.then((response) => {
        if (response.status == 200) {
          console.log("User logged in successfully");
          if (response.data.access_token) {
            setCookie("access_token", response.data.access_token, 1);
            return response.data;
          } else return false
        } else return false
        });
	}

	logout() {
		localStorage.removeItem("user");
	}

	register(username: string, email: string, password: string) {
		return axios.post(API_URL + "register/", {
			username,
			email,
			password,
		}).then((response) => {
      if (response.status == 201) {
        console.log("User created successfully");
        return response.data
      }
    });
	}

	getCurrentUser() {
		return JSON.parse(localStorage.getItem("user") || "{}");
	}
}

export default new AuthenticationService();
