import React, { useEffect, useState, useRef } from "react";
import styled from "styled-components";
import { Add, TrashCan } from '@carbon/icons-react';
import { TextInputSkeleton, Button, Theme, Search } from "@carbon/react";
// @ts-ignore
import { InlineLoading } from "@carbon/react";
import { Layer } from "@carbon/react";
import Table from './Table'

import type { Judgment } from '../../requests/request';

import "./judgmentTable.scss";
/*
  SEE: https://github.com/dhobi/datatables.colResize for column resizing implementation
*/

interface JudgementTableProps {
	showRowCreation: () => void;
	isSending: boolean;
	searchByKeyword: (value: string) => void;
	columnNames: string[] | null;
	tableData: Judgment[] | null;
	showEditFrame: (e: any) => void;
	hideEditFrame: (e: any) => void;
	setSelectedData: (judgment: Judgment[]) => void;
	deleteSelectedRows: () => void;
	showJudgmentPanel: () => void;
}

const JudgementTable: React.FC<JudgementTableProps> = ({ isSending, showRowCreation, searchByKeyword, columnNames, tableData, showEditFrame, hideEditFrame, setSelectedData, deleteSelectedRows, showJudgmentPanel }) => {
	const [selectedRows, setSelectedRows] = useState<Judgment[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  
	useEffect(() => {
    setLoading(tableData == null);
	}, [tableData]);

	function setSelected(rows: Judgment[]) {
		setSelectedRows(rows);
		setSelectedData(rows);
	}

	return (
		<div className="frame-31">
			<Layer level={0} className="table-buttons-container">
				<Search disabled={true} className="main-search-bar" labelText="Search judgment summary" placeholder="search judgment by keywords..." onChange={(e) => searchByKeyword(e.target.value)} />
				<Button kind="tertiary" disabled={loading} className="add-button" onClick={showRowCreation} renderIcon={Add}>
					New Judgment
				</Button>
				<Button kind="danger--tertiary" className="delete-button" disabled={selectedRows.length == 0 || isSending} onClick={deleteSelectedRows} renderIcon={TrashCan}>
					Delete Judgments
				</Button>
			</Layer>
			<Layer level={0} className="table-container-wrapper">
				{loading ? <InlineLoading status="active" iconDescription="Loading" description="Judgment list..." /> : <h2 className="table-title">Judgment list</h2>}
				<Table showJudgmentPanel={showJudgmentPanel} columnNames={columnNames} hideEditFrame={hideEditFrame} setSelectedData={setSelected} showEditFrame={showEditFrame} tableData={tableData} deleteSelectedRows={deleteSelectedRows} />
			</Layer>
		</div>
	);
};
export default JudgementTable;